* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ol,
li,
ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  vertical-align: top;
}

body {
  font-family: "Lato", sans-serif;
  /* font-family: "Shantell Sans", cursive; */
  font-weight: normal;
}
